#search-teacher .textarea-block {
    position: relative;
}

#search-teacher .textarea-block + .textarea-block {
    margin-top: 1.4rem;
}

.textarea-block label {
    font-size: 1.4rem;  
}

.textarea-block textarea {
    width: 100%;
    height: 16rem;
    min-height: 8rem;
    margin-top: 0.8rem;
    border-radius: 0.8rem;
    background: var(--color-textarea-background);
    border: 1px solid var(--color-line-in-white);
    outline: 0;
    resize: vertical;
    padding: 1.2 1.6rem;
    font: 1.6rem Archivo;
}